.barra {
    display: flex;
    flex-direction: column;
    height: auto;
    margin-top: 1.5rem;
    padding-right: 1.5rem;
        
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        margin-top: 0;
    }

}

.navegacion {

    
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding-top: 3rem;
    padding-right: 1.5rem;
    align-items: flex-end;

    visibility: hidden;
    opacity: 0;
    max-height: 0;
    
    transition: all .3s ease-in-out;

    @include tablet {
        display: flex;
        flex-direction: row;
        gap: 2rem;
        align-items: center;
        font-size: 1.8rem;
        padding: 0;
        visibility: visible;
        opacity: 1;
        max-height: fit-content;
    }

    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet {
            font-size: 1.8rem;
        }
    }

    a:hover {
        color: $verde;
    }

    .dark-mode-boton {
        width: 3rem;
        transition: all .2s ease-in-out;

        filter: invert(100%);

        &:hover {
            cursor: pointer;
            filter: invert(50%);
        }

        &:active {
            transform: scale(.8);
        }
    }
    
}

/** Menu hamburguesa **/

.mobile-menu{

    @include tablet{
        display: none;
    }

    img {
        transition: all .3s ease-in-out;
    }

    img:active{
        transform: scale(.8);
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    max-height: 500px;
       
}

.flex {
    display: flex;
    width: 100%;
    justify-content: space-between;

    @include tablet {
        width: 50%;
    }
}

