.contenedor-anuncios{
    gap: 2rem;
    display: flex;
    flex-direction: column;
    padding: 2rem 0;
    
    @include tablet {
        @include grid(2, 2rem);     
    }
    @include desktop {
        @include grid(3, 2rem);     
    }
}

.anuncio {
    background-color: $blanco ;
    border: 1px solid $blanco;
    box-shadow: -4px 4px 6px 0px ($gris);
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
       
    
    img {
        border-radius: 1rem 1rem 0 0 ;
        
    }
    .contenido-anuncio {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 2rem;
        height: 80%;
       
    }
    
    h3,p{
        margin: 0;
    }
    h3 {
        text-align: left;
        line-height: 1;
        margin-bottom: 1rem;
        font-weight: $regular;
    }

}
.precio {
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}


.iconos-caracteristicas {
    display: flex;
    justify-content: space-around;
    list-style: none;
    padding: 2rem 0 0 0;
    align-items: center;
    text-align: center;
    margin: 0;
    max-width: 40rem;

    
    li {
        display: flex;
        align-items: flex-start;
        justify-content: end;
        padding: 0;

        img {
            height: 4rem;
            
        }
        p {
            font-weight: $bold;
            font-size: 3rem;
            line-height: 0;
            margin-left: 0.5rem;
            
        }
        
    }
    
}

.resumen-propiedad .iconos-caracteristicas{
    justify-content: space-between;
}