.testimonial {
    background-color: $verde;
    font-size: 2.4rem;
    padding: 2.4rem;
    color: $blanco;
    border-radius: 1rem;

    blockquote {
        padding-left: 5rem;
        position: relative;

        &::before {
            content: '';
            background-image: url('../img/comilla.svg');
            background-repeat: no-repeat;
            width: 8rem;
            height: 6rem;
            display: block;
            position:absolute;
            top: -2rem;
            left: -3rem;
        }
    }

    p {
        text-align: right;
        color: $blanco;
    }
}