/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #f5f5f6;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.mw-40 {
  max-width: 40rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-centro {
  display: flex;
  justify-content: center;
}

.margin-bottom {
  margin-bottom: 2.5rem;
}

.no-margin-top {
  margin-top: 0 !important;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0.5rem;
  border: none;
  transition: all ease-in 0.1s;
}
.boton-amarillo:hover {
  background-color: #c77808;
}
.boton-amarillo:active {
  transform: scale(0.99);
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  border: none;
  transition: all ease-in 0.1s;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}
.boton-amarillo-block:active {
  transform: scale(0.99);
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0.5rem;
  border: none;
  transition: all ease-in 0.1s;
}
.boton-verde:hover {
  background-color: #619800;
}
.boton-verde:active {
  transform: scale(0.99);
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  border: none;
  transition: all ease-in 0.1s;
}
.boton-verde-block:hover {
  background-color: #619800;
}
.boton-verde-block:active {
  transform: scale(0.99);
}

.boton-rojo-block {
  background-color: rgb(255, 41, 76);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  border: none;
  transition: all ease-in 0.1s;
}
.boton-rojo-block:hover {
  background-color: #ff1037;
}
.boton-rojo-block:active {
  transform: scale(0.99);
}

.dark-mode {
  background-color: #222222;
}
.dark-mode p {
  color: #e1e1e1;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode a {
  color: #f7f7f7;
}
.dark-mode .anuncio {
  background-color: #3b3b3b;
  border: 1px solid #333333;
  box-shadow: -4px 4px 6px 0px #121212;
}
.dark-mode .anuncio .precio {
  color: #71B100;
}
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .iconos-caracteristicas img {
  filter: invert(90%);
}
.dark-mode .texto-nosotros blockquote {
  color: #f7f7f7;
}
.dark-mode .formulario {
  background-color: #3b3b3b;
  border: 1px solid #333333;
  box-shadow: -4px 4px 6px 0px #121212;
}
.dark-mode .formulario input:not([type=submit]), .dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #3b3b3b;
  color: #f7f7f7;
}
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario legend,
.dark-mode .formulario label {
  color: #f7f7f7;
}

/**** UI ****/
.nowebp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1.5rem 0;
}
.header img {
  width: 80%;
  max-height: 4rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  min-height: 90vh;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .contenido-header {
  min-height: 90vh;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.1;
  margin: 2rem 0 0 1rem;
}
@media (min-width: 480px) {
  .contenido-header h1 {
    line-height: 1.5;
  }
}
@media (min-width: 768px) {
  .contenido-header h1 {
    max-width: 60rem;
    line-height: 2;
  }
}

.footer {
  background-color: #333333;
  padding: 1rem;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding: 1rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  align-items: center;
  padding: 1rem;
  max-height: fit-content;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  padding: 1rem;
}

.barra {
  display: flex;
  flex-direction: column;
  height: auto;
  margin-top: 1.5rem;
  padding-right: 1.5rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    margin-top: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-top: 3rem;
  padding-right: 1.5rem;
  align-items: flex-end;
  visibility: hidden;
  opacity: 0;
  max-height: 0;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    align-items: center;
    font-size: 1.8rem;
    padding: 0;
    visibility: visible;
    opacity: 1;
    max-height: fit-content;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    font-size: 1.8rem;
  }
}
.navegacion a:hover {
  color: #71B100;
}
.navegacion .dark-mode-boton {
  width: 3rem;
  transition: all 0.2s ease-in-out;
  filter: invert(100%);
}
.navegacion .dark-mode-boton:hover {
  cursor: pointer;
  filter: invert(50%);
}
.navegacion .dark-mode-boton:active {
  transform: scale(0.8);
}

/** Menu hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  transition: all 0.3s ease-in-out;
}
.mobile-menu img:active {
  transform: scale(0.8);
}

.mostrar {
  visibility: visible;
  opacity: 1;
  max-height: 500px;
}

.flex {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .flex {
    width: 50%;
  }
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.contenedor-anuncios {
  gap: 2rem;
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #FFFFFF;
  border: 1px solid #FFFFFF;
  box-shadow: -4px 4px 6px 0px #e1e1e1;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
}
.anuncio img {
  border-radius: 1rem 1rem 0 0;
}
.anuncio .contenido-anuncio {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
  height: 80%;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio h3 {
  text-align: left;
  line-height: 1;
  margin-bottom: 1rem;
  font-weight: 400;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  justify-content: space-around;
  list-style: none;
  padding: 2rem 0 0 0;
  align-items: center;
  text-align: center;
  margin: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  display: flex;
  align-items: flex-start;
  justify-content: end;
  padding: 0;
}
.iconos-caracteristicas li img {
  height: 4rem;
}
.iconos-caracteristicas li p {
  font-weight: 700;
  font-size: 3rem;
  line-height: 0;
  margin-left: 0.5rem;
}

.resumen-propiedad .iconos-caracteristicas {
  justify-content: space-between;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
  margin: 0;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.info-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
  margin-top: 1rem;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-entrada p {
  margin: 0.5rem 0;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2.4rem;
  color: #FFFFFF;
  border-radius: 1rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 8rem;
  height: 6rem;
  display: block;
  position: absolute;
  top: -2rem;
  left: -3rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario {
  background-color: #FFFFFF;
  box-shadow: -4px 4px 6px 0px #e1e1e1;
  border-radius: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.formulario fieldset {
  border: 2px solid #e1e1e1;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 1rem;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  border-radius: 0.5rem;
  border: 2px solid #e1e1e1;
  margin-bottom: 1rem;
  padding: 1rem;
  width: 100%;
  background-color: #FFFFFF;
}
.formulario textarea {
  height: 20rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 0;
}
.formulario legend {
  background-color: #E08709;
  color: #FFFFFF;
  line-height: 1;
  padding: 0.8rem 1.6rem;
  border-radius: 0.5rem;
}
.formulario label {
  text-transform: uppercase;
  display: block;
}
.formulario .forma-contacto {
  max-width: 20rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
.formulario input[type=radio] {
  margin: 0;
  margin-left: -1rem;
  width: auto;
}

/**** Pàginas internas ****/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 0 0;
}

.alerta.error {
  color: rgb(255, 41, 76);
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  padding: 0.5rem;
  border: 1px solid rgb(255, 41, 76);
  border-radius: 0.5rem;
  margin: 1rem;
}
.alerta.exito {
  color: #71B100;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  padding: 0.5rem;
  border: 1px solid #71B100;
  border-radius: 0.5rem;
  margin: 1rem;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  text-align: center;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 1rem;
}
table.propiedades .imagenTabla {
  width: 10rem;
  margin: 0 auto;
}
table.propiedades tbody td a, table.propiedades tbody td input {
  width: 100%;
  max-width: 10rem;
  margin: 0 auto;
  margin-top: 0.5rem;
  padding: 1rem;
  font-size: 1.2rem;
  line-height: 1.1;
  font-family: "Lato", sans-serif;
}
table.propiedades tbody td a:last-of-type {
  margin-bottom: 0.5rem;
}
table.propiedades tbody tr:nth-child(odd) {
  background-color: #e1e1e1;
}

.imagen-small {
  width: 200px;
  border: 2px solid #e1e1e1;
  border-radius: 0.5rem;
}