.formulario {

    background-color: $blanco ;
    box-shadow: -4px 4px 6px 0px ($gris);
    border-radius: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
   
    fieldset {
        border: 2px solid $gris;
        border-radius: 0.5rem;
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 1rem;
    }

    input:not([type="submit"]), textarea, select {
        border-radius: 0.5rem;
        border: 2px solid $gris;
        margin-bottom: 1rem;
        padding: 1rem;
        width: 100%;
        background-color: $blanco;
    }
    textarea {
        height: 20rem;
    }
    
    
    p {
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 0;
    }

    legend {
        background-color: $amarillo;
        color: $blanco;
        line-height: 1;
        padding: 0.8rem 1.6rem;
        border-radius: 0.5rem;
    }

    label {
        text-transform: uppercase;
        display: block;
    }

    .forma-contacto{
        max-width: 20rem;
        display:flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 1.5rem;
    }
    input[type="radio"]{
        margin: 0;
        margin-left: -1rem;
        width: auto;
    }
}