.alerta {
    
    &.error {
        color: $rojo;
        font-size: 1rem;
        font-weight: $bold;
        text-transform: uppercase;
        text-align: center;
        padding: 0.5rem;
        border: 1px solid $rojo;
        border-radius: 0.5rem;
        margin: 1rem;

    }

    &.exito{
        color: $verde;
        font-size: 1rem;
        font-weight: $bold;
        text-transform: uppercase;
        text-align: center;
        padding: 0.5rem;
        border: 1px solid $verde;
        border-radius: 0.5rem;
        margin: 1rem;
        
    }
    
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    text-align: center;

    thead {
        background-color: $verde;
        
        th {
            color: $blanco;
            padding: 1rem;
        }
        
    }

    .imagenTabla {
        width: 10rem;
        margin: 0 auto;
    }

    tbody {

        td {
            a, input {
                width: 100%;
                max-width: 10rem;
                margin: 0 auto;
                margin-top: 0.5rem;
                padding: 1rem;
                font-size: 1.2rem;
                line-height: 1.1;
                font-family: $fuente_principal;
            }
            a:last-of-type{
                margin-bottom: 0.5rem;
            }
        }
        
        tr:nth-child(odd){background-color: $gris;}
    }
}

.imagen-small{
    width: 200px;
    border: 2px solid $gris;
    border-radius: 0.5rem;
}

