.dark-mode {
    background-color: #222222;

    p {
        color: #e1e1e1;
    }

    h1, h2, h3, h4, a{
        color: #f7f7f7;
    }

    .anuncio{
        background-color: #3b3b3b;
        border:1px solid $grisOscuro;
        box-shadow: -4px 4px 6px 0px #121212;
        
        .precio {
            color: $verde;
        }
        
    }

    .resumen-propiedad{
        .precio {
            color: $verde;
        }
    }
    .iconos-caracteristicas img {
        filter: invert(90%);
    }

    .texto-nosotros blockquote{
        color: #f7f7f7;
    }

    .formulario{
        background-color: #3b3b3b;
        border:1px solid $grisOscuro;
        box-shadow: -4px 4px 6px 0px #121212;
        
        input:not([type="submit"]), textarea, select {
            background-color: #3b3b3b;
            color: #f7f7f7;
        }

        p {
            color: #e1e1e1;
        }

        legend,
        label {
            color: #f7f7f7;
        }
        

    }

}