.nowebp .inicio {
    background-image: url('../img/header.jpg');
}

.webp .inicio {
    background-image: url('../img/header.webp');

}


.header{
    background-color: $grisOscuro;
    padding: 1.5rem 0;
    
    

    img {
        width: 80%;
        max-height: 4rem;

    }

    &.inicio {
        background-position: center center;
        background-size: cover;
        min-height: 90vh;
        

        @include tablet {
            min-height: 70rem;
        }
        .contenido-header {
            min-height: 90vh;
            @include tablet {
                min-height: 70rem;
            }
        }
    }
    }

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    
    h1 {
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 1.1;
        margin: 2rem 0 0 1rem;

        @include telefono {
            line-height: 1.5;
        }

        @include tablet {
            max-width: 60rem;
            line-height: 2;
        }
    }
}

